import React from 'react';
// import styles from "./UpdateDetailsForm.module.scss";
import {Button, Form, Input, InputNumber} from 'antd';
import Context from "../../../../store/Context";

const layout = {
    labelCol: {
        span: 8,
    },
    wrapperCol: {
        span: 16,
    },
};
const validateMessages = {
    required: '${label} -   ',
    types: {
        email: '   ',
        number: '  ',
    },
    number: {
        range: '${label}    ${min} - ${max}',
    },
};

export const UpdateDetailsForm = () => {

    const {userState} = React.useContext(Context);
    console.log('111111', userState)
    const onFinish = (values) => {
        alert(values)
    };

    return (
        <Form {...layout} name="nest-messages" onFinish={onFinish} validateMessages={validateMessages}>
            <Form.Item
                name={['user', 'firstName']}
                label=" "
                initialValue={userState.firstName}
                rules={[
                    {
                        required: true,
                    },
                ]}
            >
                <Input/>
            </Form.Item>

            <Form.Item
                name={['user', 'lastName']}
                label=" "
                initialValue={userState.lastName}
                rules={[
                    {
                        required: true,
                    },
                ]}
            >
                <Input/>
            </Form.Item>


            <Form.Item
                name={['user', 'email']}
                label=""
                initialValue={userState.email}
                rules={[
                    {
                        required: true,
                        type: 'email',
                    },
                ]}
            >
                <Input/>
            </Form.Item>


            <Form.Item
                name={['user', 'age']}
                label=""
                rules={[
                    {
                        required: true,
                        type: 'number',
                        min: 6,
                        max: 99,
                    },
                ]}
            >
                <InputNumber/>
            </Form.Item>

            <Form.Item wrapperCol={{...layout.wrapperCol, offset: 8}}>
                <Button type="primary" htmlType="submit">
                    
                </Button>
            </Form.Item>
        </Form>
    );
};
